.container {
  display: flex;
  flex-wrap: wrap;
  max-width: containerWidth($marginRight, $cardCount, $cardWidth);
  min-width: 1024px;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
  margin: 0 auto;

  &__card {
    margin-right: $marginRight;
    margin-bottom: 46px;

    &:nth-last-child(-n + 4) {
      margin-bottom: 0;
    }
  }

  & &__card:nth-child(4n) {
    margin-right: 0;
  }
}
